<template>
  <div class="login d-flex justify-content-center align-items-center">
    <form
      @submit.prevent="logIn()"
      style="box-shadow: 3px 2px 21px 2px rgb(0 0 0 / 20%); padding: 3rem"
    >
      <h1 class="text-center">Admin Login</h1>
      <div class="form-group" style="display: grid;">
        <div class="input-wrapper">
          <input
            type="email"
            name="電子郵件"
            class="login-input"
            id="loginEmail"
            placeholder="email"
            required
            v-model="user.email"
            style="width: 230px; padding-left: 30px"
          />
        </div>
        <div class="input-wrapper2">
          <input
            type="password"
            name="密碼"
            class="login-input"
            id="loginPassword"
            placeholder="password"
            required
            v-model="user.password"
            style="width: 230px; padding-left: 30px"
          />
        </div>
      </div>
      <button type="submit" class="btn btn-primary btn-block">LOGIN</button>
    </form>
  </div>
</template>
  
<script>
export default {
  data() {
    return {
      user: {
        email: "",
        password: "",
      },
    };
  },
  methods: {
    logIn() {
      const api = "https://course-ec-api.hexschool.io/api/auth/login";
      this.$http
        .post(api, this.user)
        .then((res) => {
          const { token } = res.data;
          const { expired } = res.data;
          // 將取得的token存入cookie
          document.cookie = `token=${token}; expires=${new Date(
            expired * 1000
          )};`;
          this.$router.push("/admin/products");
        })
        .catch(() => {
          alert("登入失敗，請重新嘗試");
        });
    },
    toFrontEnd() {
      this.$router.push("/");
    },
  },
};
</script>
  
<style>
.login {
  height: 100vh;
}

.login-input {
  border: 0;
  border-bottom: 1px solid #ccc;
  border-radius: unset;
  margin-bottom: 15px;
}

.input-wrapper, .input-wrapper2 {
  display: inline-block;
  position: relative;
}

.input-wrapper:after {
  font-family: "FontAwesome";
  content: "\f0e0";
  position: absolute;
  left: 6px;
}

.input-wrapper2:after {
  font-family: "FontAwesome";
  content: "\f084";
  position: absolute;
  left: 6px;
}
</style>
  